import React from 'react';
import PropTypes from 'prop-types';
import PropTypesHelper from './PropTypesHelper';
import IndexPathHelper from '../state/IndexPathHelper';
import CbaInterpreter from './CbaInterpreter';
import CommonActionsHelper from './CommonActionsHelper';
import CommonConfigHelper from '../config/CommonConfigHelper';

export function isExcessivePageNesting(pagePath) {
  return IndexPathHelper.getPageSegmentArray(pagePath).length >= 15;
}

export default function PageAreaRenderer({
  onClick,
  onContextMenu,
  orientation,
  embeddedPageName,
  hasTabs,
  tabsBar,
  position,
  config,
  scrollableAreaRef,
  path,
  runtime
}) {

  // get the page configuration:
  const page = runtime.pageConfigurationsManager.findPage(embeddedPageName);
  if (page === undefined) {
    return (
      <div
        style={CommonConfigHelper.buildStyleByState(position, config, false, orientation, false, runtime)}
      >
        {`Cannot find page with name ${embeddedPageName}`}
      </div>
    );
  }

  // bail out if we nest too deep:
  if (isExcessivePageNesting(path)) {
    return (
      <div
        style={CommonConfigHelper.buildStyleByState(position, config, false, orientation, false, runtime)}
      >
        Page nesting exceeds maximum nesting depth.
      </div>
    );
  }


  const childAreaStyle = {
    position: "absolute",
    [orientation]: 0,
    top: (hasTabs ? 30 : 0),
    width: "100%",
    height: "100%",
    overflow: "auto"
  }

  const pagePath = IndexPathHelper.appendPageSegmentsToPath(path, IndexPathHelper.buildPageSegment(embeddedPageName));

  return (
    <div
      onClick={onClick}
      onContextMenu={onContextMenu}
      title={CommonConfigHelper.buildTitle(config)}
      style={CommonConfigHelper.buildStyleByState(position, config, false, orientation, false, runtime)}
      data-cba-id={config.pageEditId}
      role="presentation"
      className={CommonConfigHelper.getCssClassNamesValue(config)}
    >
      {hasTabs && tabsBar}
      <div
        style={childAreaStyle}
        onScroll={CommonActionsHelper.traceUserScroll(pagePath, runtime)}
        ref={scrollableAreaRef}
      >
        <CbaInterpreter config={page.content} path={pagePath} runtime={runtime} orientation={orientation} />
      </div>
    </div>
  );

}

PageAreaRenderer.propTypes = {
  onClick: PropTypes.func.isRequired,
  onContextMenu: PropTypes.func.isRequired,
  orientation: PropTypes.string.isRequired,
  embeddedPageName: PropTypes.string.isRequired,
  hasTabs: PropTypes.bool.isRequired,
  tabsBar: PropTypes.object.isRequired,
  path: PropTypes.string.isRequired,
  position: PropTypes.object.isRequired,
  config: PropTypes.shape(PropTypesHelper.getStandardConfigPropTypes(false)).isRequired,
  scrollableAreaRef: PropTypes.object.isRequired,
  runtime: PropTypes.shape(PropTypesHelper.getStandardRuntimePropTypes()).isRequired,
}
