
export const FORMATS = {
  DE: {
    decimal: ",", grouping: "."
  },
  EN: {
    decimal: ".", grouping: ","
  }
}

/**
  * Translate a value given as number (or as string with dot as decimal separator)
  * to a string with decimal and thousand separators as specified by our format members.
  */
export function formatValue(number, withThousandSeparator, addTrailingDecimalSeparator, format) {
  if (number === null || number === undefined) return number;

  const parts = number.toString().split('.');
  if (withThousandSeparator) {
    parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, format.grouping);
  }
  return `${parts.join(format.decimal)}${addTrailingDecimalSeparator ? format.decimal : ''}`;
}
