import { Parser } from 'hot-formula-parser'
import TableHelper from './TableHelper';

/**
 * Internal helper method: Build a result structure that signals an error.
 */
function buildErrorResult(error) {
  return {
    error,
    result: null
  }
}

/**
 * Internal helper method: Build a result structure that contains a result value.
 */
function buildValueResult(value) {
  return {
    error: null,
    result: value
  }
}

/**
 * Internal helper method: Sum up the values given as nested arrays of numbers or strings representing numbers.
 */
function sumUpNestedValues(values) {
  if (typeof values === 'number') return values;
  if (typeof values === 'string') {
    const number = Number.parseFloat(values);
    if (Number.isNaN(number)) throw new Error("#NUM!");
    return number;
  }
  if (Array.isArray(values)) return values.reduce((previous, current) => previous + sumUpNestedValues(current), 0);
  return 0;
}


/**
 * Evaluate the value of a cell in the given spreadsheet table. 
 * 
 * The cells table is an array of rows, with each row being an array of cells.
 * Indices start with 0.
 * Examples: 
 *   - cellsTable[3][8] is the cell in the 9th column of the 4th row.
 * 
 * The maxCellCoordinates parameter gives the coordinates of the cell in the lower 
 * right corner of the cells table (i.e. the index of the last column in the last row).
 * 
 * We assume that the cells table contains one header row (with column titles 'A', 'B', ...)
 * and one header column with row numbers (1, 2, 3, ...)
 * Therefore, cell references like A1, B1, C5 refer to cells in the cells table with an 'offset' of 1
 * to ignore the header row and column:
 * Examples: 
 *  - A1 refers to data[1][1]. 
 *  - B1 refers to data[1][2].
 *  - C5 refers to data[5][3].
 * 
 * The cellToEvaluate parameter should not include this offset: 
 * {row:0 , col: 0} should refer to cell A1 (=data[1][1]), {row:3, col:2} should refer to cell C5 (=data[5][3]).
 * 
 * The evaluatedCellsList parameter should contain the indices of the cells that
 * are currently part of the evaluation stack (again including the offset). 
 * The cellToEvaluate should not be part of that list yet. 
 * We use this list to avoid an endless loop in case of a circular dependency.
 * 
 * We return errors if we detect inconsistencies with the formulas:
 *  - #N/A: A reference beyond the borders of our data table was detected.
 *  - #REF!: A Circular dependency was detected.
 * 
 * @param { [[string]]} cellsTable
 * @param { row: int, col: int} maxCellCoordinates
 * @param { row: int, col: int } cellToEvaluate
 * @param { [{ row: int, col: int}]} evaluatedCellsList
 * @returns { error: string, result: string} The evaluation result or an error.
 */
export default function evaluateCell(cellsTable, maxCellCoordinates, cellToEvaluate, evaluatedCellsList) {

  const dataCellCoordinates = {
    row: cellToEvaluate.row + 1,
    col: cellToEvaluate.col + 1
  }

  // check coordinate ranges:
  if (dataCellCoordinates.row < 1 || dataCellCoordinates.col < 1) {
    return buildErrorResult('#N/A');
  }
  if (dataCellCoordinates.row > maxCellCoordinates.row || dataCellCoordinates.col > maxCellCoordinates.col) {
    return buildErrorResult('#N/A');
  }

  // avoid endless loop in a circular dependeny:
  if (evaluatedCellsList.find(candidate => candidate.row === cellToEvaluate.row && candidate.col === cellToEvaluate.col) !== undefined) {
    return buildErrorResult('#REF!');
  }

  const cellContent = cellsTable[dataCellCoordinates.row][dataCellCoordinates.col];

  // return cell content if cell does not contain a formula
  if (!TableHelper.isFormula(cellContent)) {
    return buildValueResult(cellContent);
  }

  // extract formula from cell content
  const formula = cellContent.slice(1);

  // evaluate formula 
  const extendedCellsList = evaluatedCellsList.slice();
  extendedCellsList.push(cellToEvaluate);
  const parser = new Parser();

  parser.on('callCellValue', (referredCoordinates, done) => {
    const recursiveResult = evaluateCell(cellsTable, maxCellCoordinates, {
      row: referredCoordinates.row.index,
      col: referredCoordinates.column.index
    }, extendedCellsList);
    if (recursiveResult.error !== null) {
      throw new Error(recursiveResult.error);
    }
    return done(recursiveResult.result)
  })

  parser.on('callRangeValue', (startCellCoordinates, endCellCoordinates, done) => {
    const resultRows = [];

    for (let row = startCellCoordinates.row.index; row <= endCellCoordinates.row.index; row += 1) {
      const resultRow = [];
      for (let col = startCellCoordinates.column.index; col <= endCellCoordinates.column.index; col += 1) {
        const recursiveResult = evaluateCell(cellsTable, maxCellCoordinates, {
          row, col
        }, extendedCellsList);
        if (recursiveResult.error !== null) {
          throw new Error(recursiveResult.error);
        }
        resultRow.push(recursiveResult.result);
      }
      resultRows.push(resultRow);
    }
    return done(resultRows)
  })

  /**
   * Introduce German 'SUMME' function (calculates the sum of all given values).
   */
  parser.setFunction('SUMME', params => sumUpNestedValues(params));

  return parser.parse(formula);
}
