import React from 'react';
import PropTypes from 'prop-types';

function buildBooleanResultString(result) {
  if (result === undefined) return "";
  return result ? "true" : "false";
}

function compareClassEntryByName(left, right) {
  if (left.name < right.name) return -1;
  if (left.name > right.name) return 1;
  return 0;
}

function sortClassEntriesByName(entries) {
  return entries.sort((left, right) => compareClassEntryByName(left, right));
}

function buildClassMemberCountMap(resultsList) {
  const classToMemberCount = new Map();
  resultsList.forEach((member) => {
    const oldEntry = classToMemberCount.get(member.class);
    const newValue = oldEntry === undefined ? 1 : oldEntry + 1;
    classToMemberCount.set(member.class, newValue);
  });
  return classToMemberCount;
}

function buildAlertStyle(alert) {
  return alert ? {
    color: 'red'
  } : {}
}

function renderHitMissTable(title, resultsList, firstActiveMode) {
  const classToMemberCount = buildClassMemberCountMap(resultsList);

  if (firstActiveMode || resultsList.length === 0) return "";
  return (
    <div>
      {title}
      <table className="details">
        <thead>
          <tr>
            <th>!</th>
            <th>Name</th>
            <th>Weight</th>
            <th>Class</th>
            <th>Result text</th>
          </tr>
        </thead>
        <tbody>
          {resultsList.map((row, i) => {
            const hasActiveSiblings = classToMemberCount.get(row.class) > 1;
            return (
              <tr key={row.name} style={buildAlertStyle(hasActiveSiblings)}>
                <td className="cbaColWidth05">{ hasActiveSiblings ? "!" : ""}</td>
                <td className="cbaColWidth25">{row.name}</td>
                <td className="cbaColWidth05">{row.weight}</td>
                <td className="cbaColWidth30">{row.class}</td>
                <td className="cbaColWidth35">{row.resultText}</td>
              </tr>
            )
          })}
        </tbody>
      </table>
      <br />
    </div>
  );
}

function getResultTextForHitMissByName(hitMissName, hitMissResultsList) {
  console.log(`hitMissName is ${hitMissName}, hitMissResultsList contains names ${hitMissResultsList.map(hitMiss => hitMiss.name).join()}`)
  const matchingResult = hitMissResultsList.find(hitMiss => hitMiss.name === hitMissName);
  return matchingResult === undefined ? '' : matchingResult.resultText;
}

function renderClassTableBody(classResultsList, hitMissResultsList, firstActiveMode) {
  return sortClassEntriesByName(classResultsList).map((row, i) => (
    <tr key={row.name}>
      <td className="cbaColWidth25">{row.name}</td>
      <td className={firstActiveMode ? "cbaColWidth30" : "cbaColWidth05"}>{row.value}</td>
      { !firstActiveMode && <td className="cbaColWidth05">{buildBooleanResultString(row.result)}</td> }
      { firstActiveMode && <td className="cbaColWidth30">{getResultTextForHitMissByName(row.value, hitMissResultsList)}</td> }
      <td className="cbaColWidth30">{row.comment}</td>
    </tr>
  ));
}


function renderClassTable(title, classResultsList, hitMissResultsList, firstActiveMode) {
  if (classResultsList.length === 0) return "";
  return (
    <div>
      {`${title} (${firstActiveMode ? "first active member" : "accumulated weights"}):`}
      <table className="details">
        <thead>
          <tr>
            <th>Name</th>
            <th>{ firstActiveMode ? 'First Active' : 'Weight'}</th>
            <th>{ firstActiveMode ? 'Result Text' : 'Result'}</th>
            <th>Comment</th>
          </tr>
        </thead>
        <tbody>
          {renderClassTableBody(classResultsList, hitMissResultsList, firstActiveMode)}
        </tbody>
      </table>
      <br />
    </div>
  );
}


const ScoringModalData = (props) => {
  const { currentModalContent: scoringDetails } = props;
  const hitClassFirstActive = scoringDetails.classScoringModeHit === 'firstActive';
  const missClassFirstActive = scoringDetails.classScoringModeMiss === 'firstActive';

  return (
    <div className="cba-modal-content">
      <table className="summary">
        <thead>
          <tr>
            <th>Score result:</th>
            <td>{scoringDetails.result ? 'true' : 'false'}</td>
            <th>Execution Time:</th>
            <td>{scoringDetails.execTime}</td>
            <th>Total hits/Total Weight:</th>
            <td>{`${scoringDetails.nbHits}/${scoringDetails.hitWeight}`}</td>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th>Credit Class:</th>
            <td>{scoringDetails.creditClass}</td>
            <th>Reaction Time:</th>
            <td>{scoringDetails.reactionTime}</td>
            <th>Total misses/Total Weight:</th>
            <td>{`${scoringDetails.nbMisses}/${scoringDetails.missWeight}`}</td>
          </tr>
          <tr>
            <th>Credit Weight:</th>
            <td>{scoringDetails.creditWeight}</td>
            <th>Nb. of Interactions:</th>
            <td>{scoringDetails.nbInteractions}</td>
          </tr>
          <tr>
            <th>Result text:</th>
            <td>{scoringDetails.resultText}</td>
            <th />
            <td />
          </tr>
        </tbody>
      </table>

      <br />
      <div className="detailsWrapper">
        {renderHitMissTable("Hits:", scoringDetails.hitRows, hitClassFirstActive)}
        {renderHitMissTable("Misses:", scoringDetails.missRows, missClassFirstActive)}
        {renderClassTable("Hit Classes", scoringDetails.hitClassRows, scoringDetails.hitRows, hitClassFirstActive)}
        {renderClassTable("Miss Classes", scoringDetails.missClassRows, scoringDetails.missRows, missClassFirstActive)}
      </div>
    </div>
  );
}


ScoringModalData.propTypes = {
  currentModalContent: PropTypes.object.isRequired
}

export default ScoringModalData;
