import MenuItemTree from '../components/MenuItemTree';
import RenderingHelper from '../components/RenderingHelper';
import Utils from '../utils/Utils';

/**
 * Helper to run a story of trace log events as replay.
 */
export default class ReplayRunner {

  // ---------- public API ---------------------------------------------------------------------

  /**
   * Build a replay runner. 
   */
  constructor() {
    this.nowProvider = () => Date.now();
    this.delayProcessor = (delayMillis, action) => {
      console.info(`Replay step with delay: ${delayMillis}`);
      setTimeout(action, delayMillis);
    }
  }

  /**
   * Set a non-standard "now" timestamp provider.
   * 
   * Test code might wish control the value used for "now".
   * Production code should not use this method but rely on the default standard implementation
   * that we provide implicitly. 
   */
  setNowProvider = (nowProvider) => {
    this.nowProvider = nowProvider;
  }

  /**
   * Set a non-standard delay processor.
   * 
   * Test code might wish to shortcut delays and collect delay intervals explicitly instead.
   * Production code should not use this method but rely on the default standard implementation
   * that we provide implicitly. 
   */
  setDelayProcessor = (processor) => {
    this.delayProcessor = processor;
  }


  /**
   * Run a replay of the given replay steps.
   */
  replay = (story, finishedAction, settings, headerButtons, courseForNavigator, testsForNavigator, runtime) => {

    this.replayFromTaskStartingPoint(
      story,
      0,
      {
        settings, headerButtons, courseForNavigator, testsForNavigator
      },
      finishedAction,
      runtime
    );
  }


  // ---------- private stuff ---------------------------------------------------------------------


  /**
   * Run a replay of the given replay steps starting with the step at the given from index.
   */
  replayFromTaskStartingPoint = (story, fromIndex, appSettings, finishedAction, runtime) => {
    // Get the initializing sequence for the next task:
    const initializingTriple = ReplayRunner.grabInitializingTriple(story, fromIndex);
    if (initializingTriple === undefined) {
      console.info(`Could not find task start for replay -> replay command aborted.`);
      return;
    }

    const { taskSwitchEntry, fullStateEntry, tasksViewVisibleEntry, stateChangeEntries, nextIndex } = initializingTriple;

    const { taskManager, app } = runtime;

    // Initialize the state before showing the initial task:
    taskManager.preloadTasksState(fullStateEntry.details);
    stateChangeEntries.forEach((step) => {
      const { stateType, stateDelta } = step.details;
      ReplayRunner.processStateChangeStep(stateType, stateDelta, runtime);
    })

    // Show the initial task:
    const { newTest, newItem, newTask } = taskSwitchEntry.details;
    // The execution environment might want to use different global settings than the original assessment.
    // --> Ignore settings specified in TasksViewVisible entry and use current settings in TaskPlayer instead.
    const { settings, headerButtons, courseForNavigator, testsForNavigator } = appSettings;
    app.showReplay(newTest, newItem, newTask, settings, headerButtons, courseForNavigator, testsForNavigator);

    // Replay the remaining steps:
    const initialReplayTick = tasksViewVisibleEntry.details.replayTick === undefined ? 0 : tasksViewVisibleEntry.details.replayTick;
    const initialTimestamp = this.nowProvider();

    this.continueStoryExecution(
      story,
      nextIndex,
      initialReplayTick,
      initialTimestamp,
      nextStepIndex => this.executeNextReplayStep(story, nextStepIndex, appSettings, finishedAction, runtime),
      finishedAction
    );

  }

  /**
   * Continue to replay the given story of replay step entries. 
   * 
   * We run further steps in the story starting from the nextStepIndex. 
   *
   * As long as 
   *  - the steps don't contain a replayTick value or 
   *  - their replayTick time is almost reached already
   * we execute steps at once, i.e. we call the stepAction for them.
   * 
   * At the first step that requires postponement due to its replayTick value,
   * we schedule another call to ourself with an updated nextStepIndex.
   * 
   * If we reach the end of the story we trigger the doneAction.
   * 
   * @param {*} story The complete story of replay steps.
   * @param {*} nextStepIndex The index of the next replay step to be executed.
   * @param {*} initialReplayTick The replayTick of the initial TasksViewVisible step.
   * @param {*} initialTimestamp The time when the replay of the complete story started.
   * @param {*} appSettings The settings to use for starting the App for a task.
   * @param {*} stepAction The action to perform on each replay step. This should return true if it takes over processing of all remaining steps, false otherwise.
   * @param {*} doneAction The action to perform when the story ends.
   */
  continueStoryExecution = (story, nextStepIndex, initialReplayTick, initialTimestamp, stepAction, doneAction) => {
    let currentStepIndex = nextStepIndex;
    let currentStep = story[currentStepIndex];
    let delayMillis = ReplayRunner.calculateDelayMillisByNow(initialReplayTick, initialTimestamp, currentStep, this.nowProvider);
    while (currentStep !== undefined && delayMillis < 100) {
      if (stepAction(currentStepIndex)) {
        return;
      }
      currentStepIndex += 1;
      currentStep = story[currentStepIndex]
      delayMillis = ReplayRunner.calculateDelayMillisByNow(initialReplayTick, initialTimestamp, currentStep, this.nowProvider);
    }

    if (currentStep !== undefined) {
      this.delayProcessor(delayMillis, () => {
        this.continueStoryExecution(story, currentStepIndex, initialReplayTick, initialTimestamp, stepAction, doneAction);
      })
    } else {
      doneAction();
    }

  }

  /**
   * Execute the given replay step immediately.
   * 
   * If it takes over all remaining steps the method returns true, otherwise false.
   */
  executeNextReplayStep(story, nextStepIndex, appSettings, doneAction, runtime) {
    const currentStep = story[nextStepIndex];
    let dummyEvent;

    switch (currentStep.type) {
      case 'StateChange':
        {
          const { stateType, stateDelta } = currentStep.details;
          ReplayRunner.processStateChangeStep(stateType, stateDelta, runtime);
        }
        return false;
      case 'FullState':
        // TODO: ignore for now but we should validate this later on
        return false;
      case 'RenderTriggered':
        {
          const component = runtime.componentDirectory.findComponent(currentStep.details.indexPath);
          if (component !== undefined) {
            RenderingHelper.triggerRendering(component);
          } else {
            console.log(`Component missing for rendering -> trigger render from top`);
            runtime.taskManager.handleTopLevelPageChange()
          }
        }
        return false;
      case 'TaskSwitch':
        if (currentStep.details.newTask === undefined) {
          // stop task entry 
          runtime.app.showWaiting();
          return false;
        } else {
          // start task entry 
          this.replayFromTaskStartingPoint(story, nextStepIndex, appSettings, doneAction, runtime);
          return true;
        }
      case 'ContextMenu':
        if (currentStep.details.shouldOpen) {
          dummyEvent = new Event(currentStep.details.event.type);
          dummyEvent.clientX = currentStep.details.event.clientX;
          dummyEvent.clientY = currentStep.details.event.clientY;

          runtime.contextMenu.openMenuItemTree({
            entries: currentStep.details.entries,
            event: dummyEvent
          });
        } else {
          MenuItemTree.closeMenuItemTree();
        }

        return false;
      case 'ScrollTriggered': {
        const component = runtime.componentDirectory.findComponent(currentStep.details.path);
        const { scrollTop, scrollLeft } = currentStep.details;

        if (!component && !component.containerRendererRef) {
          return false;
        }

        let htmlElement = component.containerRendererRef.current;

        while (getComputedStyle(htmlElement).overflow === 'hidden') {
          htmlElement = htmlElement.parentElement;

          if (htmlElement === document.body) {
            return false;
          }
        }

        Utils.doScroll(scrollTop, scrollLeft, htmlElement)
        return false;
      }
      default:
        // just ignore irrelevant story events
        return false;
    }
  }

  /**
   * Update the state managers due to a 'StateChange' step.
   */
  static processStateChangeStep(stateType, stateDelta, runtime) {
    switch (stateType) {
      case 'ComponentState':
        {
          const { pathId, state } = stateDelta;
          runtime.componentStateManager.registerStateChangeFromSnapshot(pathId, state);
        }
        break;
      case 'PresenterStateManagerState':
        {
          const { taskPath, state } = stateDelta;
          runtime.presenterStateManager.injectStateChangeAtReplay(taskPath, state);
        }
        break;
      case 'TaskNavigatorStateManagerState':
        {
          const { name, value } = stateDelta;
          runtime.taskNavigatorStateManager.injectStateChangeAtReplay(name, value);
        }
        break;
      case 'IncidentsAccumulatorState':
        {
          const { taskPath, state } = stateDelta;
          runtime.incidentsAccumulator.injectStateChangeAtReplay(taskPath, state);
        }
        break;
      case 'VariableManagerState':
        {
          const { taskPath, name, value } = stateDelta;
          runtime.variableManager.injectStateChangeAtReplay(taskPath, name, value);
        }
        break;
      case 'CalculatorState':
        {
          const { taskPath, state } = stateDelta;
          runtime.calculatorsManager.injectStateChangeAtReplay(taskPath, state);
        }
        break;
      case 'ClipboardState':
        {
          const { state } = stateDelta;
          runtime.clipboardManager.injectStateChangeAtReplay(state);
        }
        break;
      default:
      {
        console.warn(`Unknown StateChange replay step type ignored!`);
      }
    }
  }

  /**
   * Helper method that calculates the delay required before executing the given step.
   * 
   * If the step does not specify a replay tick value, the delay is zero.
   */
  static calculateDelayMillisByNow(initialReplayTick, initialTimestamp, step, nowProvider) {
    if (step === undefined || step.replayTick === undefined) return 0;
    return ReplayRunner.calculateDelayMillis(initialReplayTick, initialTimestamp, step.replayTick, nowProvider());
  }

  /**
   * Helper method that calculates the delay required before executing a step with the given replay tick value.
   * 
   * The delay is never negative. 
   */
  static calculateDelayMillis(initialReplayTick, initialTimestamp, stepReplayTick, nowTimestamp) {
    return Math.max(0, (stepReplayTick - initialReplayTick) - (nowTimestamp - initialTimestamp));
  }

  /**
   * Helper method that extracts the initializing triple of log entries TaskSwitch+FullState+TasksViewVisible 
   * from the given replay trace file.
   * 
   * The method returns undefined if it cannot detect a proper triple. 
   * Besides the initializing triple it returns all StateChange events 
   * appearing between the FullState and the TasksViewVisible log entries.
   */
  static grabInitializingTriple(story, nextIndex) {
    let entryIndex;

    // scroll down until we see the first TaskSwitch entry that starts a task:
    let tripleStartFound = false;
    for (entryIndex = nextIndex; entryIndex < story.length && !tripleStartFound;) {
      const currentStep = story[entryIndex];
      if (currentStep.type === 'TaskSwitch' && currentStep.details.newTask !== undefined) {
        tripleStartFound = true;
      } else {
        entryIndex += 1;
      }
    }
    if (!tripleStartFound) return undefined;
    const taskSwitchEntry = story[entryIndex];

    // scroll down through StateChange entries until we find the FullState entry:
    entryIndex += 1;
    let nextEntry = story[entryIndex];
    while (nextEntry !== undefined && nextEntry.type === 'StateChange') {
      entryIndex += 1;
      nextEntry = story[entryIndex];
    }
    if (nextEntry === undefined || nextEntry.type !== 'FullState') return undefined;
    const fullStateEntry = nextEntry;

    // scroll down through StateChange entries and remember them until we find the TasksViewVisible entry:
    entryIndex += 1;
    const stateChangeEntries = [];
    nextEntry = story[entryIndex];
    while (nextEntry !== undefined && nextEntry.type === 'StateChange') {
      stateChangeEntries.push(nextEntry);
      entryIndex += 1;
      nextEntry = story[entryIndex];
    }
    if (nextEntry === undefined || nextEntry.type !== 'TasksViewVisible') return undefined;
    const tasksViewVisibleEntry = nextEntry;

    return {
      taskSwitchEntry,
      fullStateEntry,
      tasksViewVisibleEntry,
      stateChangeEntries,
      nextIndex: entryIndex + 1
    }
  }


}
