export default class EvaluatorHelper {

  static ANGLE_DEGREE = "Degree";

  static ANGLE_RADIAN = "Radian";

  static fak = (n) => {
    if (n < 0 || n > 170) {
      return NaN;
    } else if (!EvaluatorHelper.isInt(n)) {
      return NaN;
    } else if (n <= 1) {
      return 1;
    } else {
      return n * EvaluatorHelper.fak(n - 1);
    }
  }

  static nthroot = (x, n) => {
    const negate = n % 2 === 1 && x < 0;
    if (negate) x = -x;
    const possible = x ** (1 / n);
    const check = possible ** n;
    if (Math.abs(x - check) < 1 && ((x > 0) === (check > 0))) {
      return negate ? -possible : possible;
    }
    return NaN;
  }

  static isInt = n => n % 1 === 0;

  static isBigger = displayWidth => displayWidth > 10;


  static isMultipleOfPi(value, frequency, offset, deg) {
    const unit = deg ? 180 : Math.PI;
    return ((value - offset*unit) / frequency / unit) % 1 === 0;
  }

}
