import StateAttributeAccess from '../state/StateAttributeAccess';
import IndexPathHelper from '../state/IndexPathHelper';
import ComponentStateHelper from '../state/ComponentStateHelper';
import PathTranslationHelper from '../state/PathTranslationHelper';

/**
 * Helper methods to implement the 'propagation group' mechanism.
 * 
 * A component can carry a list of 'propagation groups':
 *  - Each group is identified by a group name and contains a list of index paths to components that are members of the group.
 * 
 * Some operators in the TermEvaluator support an optional group name parameter.
 * Specifying such a group name will propagate the operator call to all members of the specified group then:
 *  - The absoluteUserDefinedId in the parameters identifies the component bearing the propagation group list.
 *  - The groupName parameter selects the group from the list of propagation groups. 
 *  - The operator's action is applied to all members of the selected group instead of the component bearing the group list.
 *  - The operator's action is not applied to the component bearing the group list itself. 
 * 
 */

export default class PropagationHelper {

  /**
   * Build the select-group controller settings for a potential propagation-group controller component.
   */
  static addPropagationGroupControllerState(pathState, configProps) {
    const config = configProps.propagationGroups;
    if (config !== undefined) {
      const controllerState = config.map((value, index, all) => ({
        name: value.name,
        memberIndexPaths: value.memberIndexPaths === undefined ? [] : [...value.memberIndexPaths]
      }));
      StateAttributeAccess.setPropagationGroupControllerState(pathState, controllerState);
    }
  }


  /**
   * Apply the given action to the members of the given propagation group of the given propagation group controller component.
   * 
   * The action must accept one parameter: the index path of the component to apply the action to.
   */
  static applyToPropagationGroup(controllerIndexPath, groupName, appliedAction, runtime) {
    const matchingGroups = PropagationHelper.getMatchingPropagationGroups(controllerIndexPath, groupName, runtime);
    matchingGroups.forEach((group) => {
      PropagationHelper.applyActionToGroupMembers(controllerIndexPath, group, appliedAction);
    });
  }

  /**
   * Convenience method: Apply an attribute setting to the component itself or to the propagation group members if a group name is given.
   */
  static applySimpleSetter(controllerUserDefPath, groupName, getter, setter, value, triggerRendering, runtime) {
    if (groupName === undefined) {
      ComponentStateHelper.updateStateAttributeByUserDefPath(getter, setter, value, controllerUserDefPath, runtime, true);
    } else {
      const controllerPathId = PathTranslationHelper.getIndexPathForUserDefPath(controllerUserDefPath, runtime);
      PropagationHelper.applyToPropagationGroup(controllerPathId, groupName,
        (componentPath) => {
          ComponentStateHelper.updateStateAttribute(getter, setter, value, componentPath, runtime, triggerRendering)
        },
        runtime);
    }
  }

  // --------------- private stuff ----------------------------------------------------------------------------------------


  /**
   * Private helper method: Get matching propagation group configuration for given propagation group controller and group name.
   */
  static getMatchingPropagationGroups(controllerIndexPath, groupName, runtime) {
    const { componentStateManager } = runtime;
    const fullControllerState = componentStateManager.findOrBuildStateForPathId(controllerIndexPath, runtime);
    const propagationsGroupsState = StateAttributeAccess.extractPropagationGroupControllerState(fullControllerState);
    if (propagationsGroupsState === undefined) {
      console.log(`Ignored request to propagate some action for a component that isn't a propagation-group controller: ${controllerIndexPath}`);
      return [];
    }
    return propagationsGroupsState.filter(group => group.name === groupName);
  }

  /** 
   * Private helper method: Apply the given action to the member paths in the given group configuration.
  */
  static applyActionToGroupMembers(controllerIndexPath, groupConfiguration, action) {
    groupConfiguration.memberIndexPaths.forEach((memberPathPageSegment) => {
      const memberIndexPath = IndexPathHelper.appendPageSegmentsToPath(IndexPathHelper.dropPageSegmentFromPath(controllerIndexPath), memberPathPageSegment);
      action(memberIndexPath);
    })
  }

}
