import * as ValueFormatter from "./ValueFormatter";
import EvaluatorHelper from "./EvaluatorHelper";

/**
 * Render a value given as string with dot as decimal separator (and maybe a trailing decimal dot)
 * to the string representation used for display to the user.
 * 
 * The transformation takes into account: 
 *  - the format (specifying the characters to use as decimal separator and thousands grouping separator)
 *  - whether to include thousands grouping separators
 *  - the display width 
 * 
 * The renderer compresses the value (by rounding) if it would take too much space in the display. 
 * (In fact we support two display width classes only: small/big. And the compressing is a bit weird, see below...)
 */
export default class Renderer {

  static msg = {
    errorMessage: "Err",
    nroot: "nroot"
  };

  /**
   * Format a value given as string with dot as decimal separator
   * according to our format and withThousandSeparator members. 
   * 
   * We implicitly translate 'NaN, 'Inf', 'Error' to the string 'Err'.
   * We implicitly translate very long numbers to an exponential notation that fits into our display width.
   * We preserve a trainling dot by translating it into a trailing decimal separator character.
   */
  static render = (value, displayWidth, withThousandSeparator, format) => {
    const bigger = EvaluatorHelper.isBigger(displayWidth);

    if (value.match(/NaN|Inf|Error/)) {
      return Renderer.msg.errorMessage;
    }

    const trailingDot = value.match(/\.$/);
    const valueWithoutTrailingDot = trailingDot ? value.substring(0, value.length - 1) : value;

    if (valueWithoutTrailingDot.length === 0) {
      return value;
    }

    const shortenedResult = Renderer.shortenValue(valueWithoutTrailingDot, bigger);
    const unformattedResult = Renderer.limitFractionalDigits(shortenedResult, bigger);
    const formattedResult = ValueFormatter.formatValue(unformattedResult, withThousandSeparator, trailingDot, format);

    return formattedResult;
  }

  // ----------------- private stuff ------------------------------------------------

  static shortenValue = (value, bigger) => {
    const valAbs = Math.abs(+value);
    if (!bigger && valAbs >= 1e+10) {
      return (+value).toExponential(8).toString();
    }
    if (valAbs >= 1e+16) {
      return (+value).toExponential(13).toString();
    }
    return value;
  }

  /**
   * Returns a string representation of the given numerical value with a limited number of fractional digits.
   * 
   * If bigger is true, it allows up to 15 fractional digits.
   * If bigger is false and there is an exponent, it allows 6 fractional digits.
   * If bigger is false and there is no exponent, it allows 8 fractional digits.
   * 
   * The method applies proper rounding to reduce the number of fractional digits.
   * 
   */
  static limitFractionalDigits(value, bigger) {
    const valueComponents = Renderer.splitValue(value);

    if (valueComponents.fractionalPart === undefined) {
      // the easy case: nothing to change:
      return value;
    }

    const targetDigitsCount = Renderer.calculateTargetDigitCount(bigger, valueComponents.exponentPart !== undefined);
    const { digits: reducedFractionalDigits, overflow: roundingOverflow } = Renderer.roundFractionalDigits(valueComponents.fractionalPart, targetDigitsCount);

    const integralPart = +valueComponents.integralPart + (roundingOverflow ? 1 : 0); // transfer overflow from digits rounding
    const fractionalPart = reducedFractionalDigits !== '' ? `.${reducedFractionalDigits}` : ''; // drop decimal point if no fractional digits are left
    const exponentPart = valueComponents.exponentPart === undefined ? '' : `e${valueComponents.exponentPart}`; // rebuild the exponent part

    return `${valueComponents.sign}${integralPart}${fractionalPart}${exponentPart}`;

  }

  static splitValue(value) {
    if (value === undefined || value === null) return {};

    const [beforeDecimalPart, postDecimalPart] = value.split('.');
    const sign = (beforeDecimalPart[0] === '-') ? '-' : '';
    const integralPart = beforeDecimalPart.replace(/^-/, '');
    const [fractionalPart, exponentPart] = postDecimalPart ? postDecimalPart.split('e') : [postDecimalPart, undefined];
    return {
      sign,
      integralPart,
      fractionalPart,
      exponentPart
    };
  }

  static calculateTargetDigitCount(bigger, withExponent) {
    if (bigger) return 15;
    if (withExponent) return 6;
    return 8;
  }


  /**
   * Round the given fractional digits to the given number of digits remaining.
   */
  static roundFractionalDigits(digits, targetDigitsCount) {
    if (targetDigitsCount > digits.length) {
      // digits fit in -> nothing to change
      return {
        digits,
        overflow: false
      }
    }
    const numberForRounding = +(`1.${digits}`);
    // add one digit for the integral 1:
    const roundedValue = numberForRounding.toPrecision(targetDigitsCount + 1).toString();
    const overflowOccured = roundedValue >= 2;
    return {
      digits: overflowOccured ? '' : roundedValue.substr(2).padEnd(targetDigitsCount, "0"), // skip integral part and pad trailing 0s.
      overflow: overflowOccured
    }
  }


}
