import StateManagerHelper from '../../state/StateManagerHelper';
import EvaluatorHelper from "./EvaluatorHelper"

/**
 * Mechanism to evaluate formulas built with binary operators
 * taking operator precedence (ranking) into account.
 * 
 * Formulas are built by sending pairs of a value and a (value terminating) operator 
 * via calls of calc(operator, value). 
 * 
 * Supported operators are (with ascending precedence):
 *  '=', 
 *  '+', '-' (with alternate symbol '' (long dash)),  
 *  '*' (with alternate symbol ''), '/' (with alternate symbol '')
 *  'yx' (calculating x^y), 'xy', 'log' and 'EE'
 *
 * The calc(operator, value) call will evaluate the formula given up till now
 * as far as possible taking operator precedence into account.
 * 
 * Sending the '=' operator will calculate the final result and erase the formula.
 * 
 * The mechanism supports a special feature for repeated '=' calls:
 * If multiple '=' calls follow each other without any other operators between,
 * the calc method will return the following calculation result:
 *  <operand of current '=' call> <operator before first '=' call> <operand of first '=' call>
 * Example call sequence:
 *  - call('+', 420) returns 420 (nothing to caculate yet)
 *  - call('-', 30)  returns 450 (same precedence, can calculate 420 + 30 already)
 *  - call('=', 70)  returns 380 (formula complete, total result is 420 + 30 - 70)
 *  - call('=', 200) returns 130 (applies - 70 to new given value 200, i.e. 200 - 70)
 *  - call('=', 100) returns  30 (applies - 70 to new given value 100, i.e. 100 - 70)
 */ 
export default class ExpressionEvaluator {

  static RANK = {
    '=': 0,
    '+': 1,
    '-': 1,
    '/': 2,
    '*': 2,
    yx: 3,
    'xy': 3,
    log: 3,
    EE: 3
  };

  constructor() {
    this.reset();
  }

  // ------------------ public API --------------------------------------------------

  /**
   * Get our full state for the task state backup/restore mechanism.
   */
  getFullState = () => ({
    stack: StateManagerHelper.deepCopy(this.stack),
    num: this.num,
    repeatingRunStatus: StateManagerHelper.deepCopy(this.repeatingRunStatus)
  });

  /**
   * Set our internal full state (obtained by getFullState()) to be used by the task state backup/restore mechanism.
   */
  restoreState = (state) => {
    this.stack = StateManagerHelper.deepCopy(state.stack);
    this.num = state.num;
    this.repeatingRunStatus = StateManagerHelper.deepCopy(state.repeatingRunStatus);
  }

  /**
   * Extend the current formula with the given value followed by the given operator
   * and return the result of the formula evaluation (as far as evaluation is possible already
   * due to operator precedences).
   */
  calc = (val, operator) => {

    const key = this.translateKey(operator);

    this.updateRepeatingRunStatus(key, val);

    if (this.isStackEmpty()) {

      if (key === '=') {
        // New operand with '=' operator received.
        if (this.repeatingRunIsActive()) {
          // Operator and second operand for the repeating runs are there already.
          // -> Do a repeating '=' calculation with the new first operand and return that as result.
          //    (The stack remains empty.)
          return `${this.calcValue(val, this.getRepeatingRunKey(), this.getRepeatingRunValue())}`;
        } else {
          // pressing '=' without anything in stack
          // -> Return given value as result.
          //    (The stack remains empty.)
          return `${val}`;
        }
      } else {
        // First operand with first binary operator received (maybe after stack reset by the last '=' operator).
        // -> Create first stack entry waiting for second operand and return the given value as result.
        this.pushStackEntry(val, key);
        return `${val}`;
      }

    } else {

      let returnValue = 0;

      // the rank of the new key
      const newRank = this.getRankForKey(key);

      // the previous key and its rank
      const previousKey = this.peekStackKey();
      const previousValue = this.peekStackValue();

      if (newRank > this.getRankForKey(previousKey)) {
        // The new operation has higher precendence than the previous one (e.g. +, *)
        // -> An operator with even higher precedence might follow. Therefore the given value might belong to that one
        //    and we have to postpone the evaluation.
        // -> Add a new pending entry to the stack.
        this.pushStackEntry(val, key);
        returnValue = val;
      } else {
        // The new operation has same or lower precendence than the previous one (e.g. *, + or *, *)
        // -> Evaluate previous operator with its first operand and the current value as second operand
        //    and pop the pending operation
        const result = this.calcValue(previousValue, previousKey, val);
        this.popStackEntry();

        // We might be able to go even further down the stack:
        if (!this.isStackEmpty() && newRank <= this.getRankForKey(this.peekStackKey())) {
          // The new operation has lower precendence than 2 steps ago -> (e.g.  +, *, = or *, yx, +)
          // -> Recursive call with the calculated value and our current operator.
          returnValue = this.calc(result, key);
        } else {
          // The new operation has a higher precedence the the one 2 steps ago (or there is nothing left in the stack). 
          // -> We have to wait for further input.
          // -> Push the result as first operand together with the new operator on the stack. 
          this.pushStackEntry(result, key);
          returnValue = result;
        }
      }

      // erase the stack if we got a '='
      if (key === '=') {
        this.resetStack();
      }

      return `${returnValue}`;

    }


  }

  /**
   * Erase the formula given so far and drop the repeating run status.
   */
  reset = () => {
    this.resetStack();
    this.resetRepeatingRun();
  }

  // ----------------- calculate complete operator expression -----------------------

  // operands are strings!
  calcValue = (o1, operator, o2) => {
    let result;
    switch (operator) {
      case 'log':
        result = Math.log(o1) / Math.log(o2);
        break;
      case 'yx':
        result = o1 ** o2;
        break;
      case 'xy':
        result = EvaluatorHelper.nthroot(o1, o2);
        break;
      case 'EE':
        result = o1 * (10 ** o2);
        break;
      case '+':
        result = (+o1) + (+o2);
        break;
      case '-':
        result = o1 - o2;
        break;
      case '*':
        result = o1 * o2;
        break;
      case '/':
        result = o1 / o2;
        break;
      default:
        throw new Error(`NO case for operator ${operator}`);
    }
    return result;
  }


  // ------------ the repeating run status structure ------------------
  // The repeating run status is used to process repeated inputs of an operand with a '='-key 
  // It contains the operator to evaluate and the static second operand to use for each operand that arrives with '='-key.
  // The lifecycle is: 
  //  - Operator other than '=' arrives: Remember operator in run status, drop second operand value in run status. 
  //    The repeat feature is inactive now.
  //  - First operator '=' arrives after any other operator: Save operand value in run status. 
  //    The repeat feature is active now.
  //  - Another operator '=' arrives after an '=' operator: Use the accompanying value as first operand, the 
  //    operator stored in the run status as operator and the value stored in the run status as second operand
  //    and evaluate that as result. (After an '=' operator the stack is always empty.)

  resetRepeatingRun = () => {
    this.repeatingRunStatus = {
      key: undefined,
      value: undefined
    };
  }

  updateRepeatingRunStatus = (key, val) => {
    if (key !== '=') {
      // repeating run is broken by new operator
      // -> erase old second operand and remember new operator for potential next run
      this.setRepeatingRunValue(undefined);
      this.setRepeatingRunKey(key);
    } else if (this.getRepeatingRunValue() === undefined) {
      // first '=' operator after another operator sets second operand for repeat calculations
      this.setRepeatingRunValue(val);
    }
  }

  repeatingRunIsActive = () => this.getRepeatingRunKey() !== undefined && this.getRepeatingRunValue !== undefined;

  getRepeatingRunValue = () => this.repeatingRunStatus.value;

  getRepeatingRunKey = () => this.repeatingRunStatus.key;

  setRepeatingRunKey = (key) => {
    this.repeatingRunStatus.key = key;
  }

  setRepeatingRunValue = (value) => {
    this.repeatingRunStatus.value = value;
  }


  // ------------ the stack structure --------------------------
  // The stack is an array of [val, key] entries. 
  // Each entry represents a calculation step with a binary operator 
  // that waits for the second operand to become available:
  // Due to the operator ranking the value following the operator in the
  // input stream might belong as first operand to a higher ranking operator following later.
  // The val is the first operand and the key is the binary operator of the pending calculation.
  // The num member is the index of the top of the stack. 
  // (We don't drop entries from the array. We just decrease the num pointer to move 'top' of the stack.)

  resetStack = () => {
    this.stack = [];
    this.num = 0;
  }

  isStackEmpty = () => this.num <= 0;

  peekStackValue = () => this.stack[this.num - 1][0];

  peekStackKey = () => this.stack[this.num - 1][1];

  pushStackEntry = (val, key) => {
    this.stack[this.num] = [val, key];
    this.num += 1;
  }

  popStackEntry = () => {
    this.num -= 1;
    return this.stack[this.num];
  }

  // -------------- other helper methods ------------------------

  translateKey = (key) => {
    if (key === '') return '*';
    if (key === '') return '/';
    if (key === '') return '-';
    return key;
  }

  getRankForKey = key => ExpressionEvaluator.RANK[key];


}
