/**
 * Keep the currently active 'replay step'.
 */
export default class ReplayStepMonitor {

  constructor() {
    this.currentReplayStepId = 0;
  }

  // public API ------------------------------------------------------------------

  incrementReplayStep = () => {
    this.currentReplayStepId += 1;

  }

  getCurrentReplayStepId = () => this.currentReplayStepId;

  // private stuff ------------------------------------------------------------------


}
