import UrlLib from 'url';
import packageJson from '../../package.json';

/**
 * Basic helper methods used by various components.
 */
export default class Utils {


  // --------- Java objects, arrays etc. ---------------------------------------------------

  /**
   * Return the given value if it is defined, return the default value otherwise.
   * 
   * @param {*} valueMightBeUndefined The value that might be undefined.
   * @param {*} defaultValue The default value to use as replacement for an undefined value.
   */
  static withDefault(valueMightBeUndefined, defaultValue) {
    return valueMightBeUndefined === undefined ? defaultValue : valueMightBeUndefined;
  }

  /**
   * Get the value of the given property of the given base object and return the default value 
   * if the object is undefined. 
   * 
   * The method will return undefined if the base object is not undefined but does not bear a property of the given name.
   * 
   * @param {*} baseObject The base object bearing the property. This object might be undefined. We will return the given default value in that case.
   * @param {String} propertyName The property of the base object.
   * @param {*} defaultValue The default value to use if the base object is undefined.
   */
  static getPropUndefinedSafe(baseObject, propertyName, defaultValue) {
    return baseObject === undefined ? defaultValue : baseObject[propertyName];
  }


  /**
   * Is the given value a 'falsy' value (i.e. null, undefined, empty string, false, 0, NaN) or 
   * an Object that does not have any attributes (i.e. properties or methods)?
   * 
   * @param {} candidate The value to check.
   */
  static isEmptyObject(candidate) {
    return !!candidate && candidate.constructor === Object && Object.keys(candidate).length === 0;
  }

  /**
   * Does the given array contain any 'non-empty' value 
   * (i.e. something that returns a non-empty string as toString() result)?
   * 
   * @param {*} array The array to check; might be undefined or null but must be an array otherwise.
   */
  static isNonEmptyArray(array) {
    return array !== undefined && array !== null && array.length !== 0 && Utils.arrayContainsNonEmptyEntry(array);
  }

  /**
   * Does the given array contain any 'non-empty' value 
   * (i.e. something that returns a non-empty string as toString() result)?
   * 
   * @param {*} array The array to check. The array must not be undefined or null.
   */
  static arrayContainsNonEmptyEntry(array) {
    return array.some(value => value !== undefined && value !== null && value.toString() !== '');
  }

  /**
   * Does the given array contain an 'empty' value 
   * (i.e. something that returns an empty string as toString() result)?
   * 
   * @param {*} array The array to check. The array must not be undefined or null.
   */
  static arrayContainsEmptyEntry(array) {
    return array.some(value => value === undefined || value === null || value.toString() === '');
  }

  /**
   * Return a 'deep' copy of the given object.
   * 
   * The method uses JSON parse - stringify to create the copy.
   * 
   * @param {*} object The object to copy. This must be JSON 'stringifyable'. 
   */
  static deepCopy = object => JSON.parse(JSON.stringify(object));

  /**
   * Return a 'deep' copy of the given object.
   * 
   * The method uses JSON parse - stringify to create the copy.
   * If the given object is undefined/null it simply returns undefined/null.
   * 
   * @param {*} object The object to copy. This may be undefined or null. 
   */
  static safeDeepCopy = (object) => {
    if (object === undefined) {
      return undefined;
    }
    return Utils.deepCopy(object);
  }


  /**
   * Extends 'from' object with members from 'to'. If 'to' is null, a deep clone of 'from' is returned.
   * 
   * TODO: CKI: The method behavior seems to be more complex than described above: No adding is done if the
   * from value is not an Object or Array. -> Properly describe and test it or try to drop it. 
   * Currently there is no call with a to value given!
   */
  static safeDeepCopyObjects = (from, to) => {
    if (from == null || typeof from !== "object") return from;
    if (from.constructor !== Object && from.constructor !== Array) return from;
    if (from.constructor === Date
      || from.constructor === RegExp
      || from.constructor === Function
      || from.constructor === String
      || from.constructor === Number
      || from.constructor === Boolean) {
      return new from.constructor(from);
    }

    to = to || new from.constructor();

    Object.keys(from).forEach((name) => {
      to[name] = typeof to[name] === "undefined" ? Utils.safeDeepCopyObjects(from[name], null) : to[name];
    });

    return to;
  }

  // --------- Mode and feature decisions ------------------------------------------------

  /**
   * Get a version number string.
   */
  static getCbaVersionNumber() {
    return `${packageJson.version}`;
  }

  /**
   * Get a version string.
   */
  static getCbaVersion() {
    return `${packageJson.name} ${packageJson.version}`;
  }

  /**
   * Print the version string to the browser console.
   */
  static printCbaVersion() {
    console.info(`Running ${Utils.getCbaVersion()}`);
  }

  /**
   * Should we run in 'mobile mode' ?
   * 
   * We check an optional epxlicit flag in our calling URL 
   * and try some heuristic to find out ourselves.
   */
  static isMobile = () => {
    let check = Utils.isMobileModeSetAsParameter();
    // eslint-disable-next-line
    (function (a) { if (/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino|android|ipad|playbook|silk/i.test(a) || /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(a.substr(0, 4))) check = true; })(navigator.userAgent || navigator.vendor || window.opera);
    return check;
  };

  /**
   * Private helper: Is there a parameter in our URL that forces 'mobile mode'?
   */
  static isMobileModeSetAsParameter() {
    const isMobileForced = Utils.getQueryParam('forceMobileMode');
    return (`${isMobileForced}` === 'true');
  }

  /**
   * Does the device use a touch screen?
   */
  static isTouchDevice() {
    return 'ontouchstart' in window || navigator.maxTouchPoints > 0;
  }

  /**
   * Internal helper: If the requested field in the given settings object exists return it's value, 
   * return an empty string otherwise.
   * 
   * @param {*} settings Object to investigate.
   * @param {*} settingName Field to look for.
   */
  static getSettingsValueByNameSafe(settings, settingName) {
    if (settings && settings[settingName] !== undefined) {
      return settings[settingName];
    }
    return '';
  }


  /**
   * Determine the controller to use and its operation mode.
   * 
   * The method returns on of three values:
   *  - preview: Run with the internal controller; use the simplified server interactions (see class comments in ServerCalls and Controller for details).
   *  - rest-api: Run with the internal controller; use the REST API server interactions (see class comments in ServerCalls and Controller for details).
   *  - external-controller: Do not run the internal controller - wait for an external controller to take over.
   */
  static getControllerMode() {
    const defaultMode = 'external-controller';
    const configFromWindowGlobal = window.cba_runtime_config;
    if (configFromWindowGlobal === undefined || configFromWindowGlobal === null) {
      return defaultMode;
    }
    const modeFromWindowGlobal = configFromWindowGlobal.mode;
    if (modeFromWindowGlobal === undefined || modeFromWindowGlobal === null) {
      return defaultMode;
    }
    if ((typeof modeFromWindowGlobal) !== 'string') {
      console.warn(`Invalid controller mode ignored, switching to external-controller mode.`, modeFromWindowGlobal);
      return defaultMode;
    }

    const trimmedMode = modeFromWindowGlobal.trim();
    switch (trimmedMode) {
      case 'preview':
        return 'preview';
      case 'rest-api':
        return 'rest-api';
      case 'external-controller':
        return 'external-controller';
      default:
        return defaultMode;
    }
  }


  /**
   * Get configuration details for the TaskPlayer's post message API.
   */
  static getEventReceiverConfiguration() {
    return Utils.getEventReceiverConfigurationInternal(window.cba_runtime_config, window.location.href);
  }

  /**
   * Internal helper: Kernel of getEventReceiverConfiguration providing a test entry point.
   */
  static getEventReceiverConfigurationInternal(configFromWindowGlobal, urlString) {
    return {
      eventTargetWindow: Utils.getParameterValueFromUrlOrConfigStructure(configFromWindowGlobal, urlString, 'eventTargetWindow', undefined),
      eventDomainUri: Utils.getParameterValueFromUrlOrConfigStructure(configFromWindowGlobal, urlString, 'eventDomainUri', undefined),
      startTaskPlayerEvent: Utils.getParameterValueFromUrlOrConfigStructure(configFromWindowGlobal, urlString, 'startTaskPlayerEvent', undefined),
    }
  }

  /**
   * Get the initial texts for the 'We are waiting for an item configuration...' page
   * displayed by our app while waiting for configurations coming in via the TaskPlayer API.
   * 
   * These texts can be given in the window.cba_runtime_config structure (prevailing)
   * or as URL parameters (if config structure does not specify them). 
   */
  static getWaitingMessageTexts() {
    return Utils.getWaitingMessageTextsInternal(window.cba_runtime_config, window.location.href)
  }


  /**
   * Internal helper: Kernel of getWaitingMessageTexts providing a test entry point.
   */
  static getWaitingMessageTextsInternal(configFromWindowGlobal, urlString) {
    return {
      primary: Utils.getParameterValueFromUrlOrConfigStructure(configFromWindowGlobal, urlString, 'initialWaitingMessagePrimary', 'We are waiting to receive the test configuration...'),
      secondary: Utils.getParameterValueFromUrlOrConfigStructure(configFromWindowGlobal, urlString, 'initialWaitingMessageSecondary', 'If this waiting takes more than a few seconds inform your test administrator.'),
    }
  }

  static getParameterValueFromUrlOrConfigStructure(configFromWindowGlobal, urlString, parameterName, defaultValue) {
    // try configuration structure first:
    if (configFromWindowGlobal !== undefined && configFromWindowGlobal !== null
      && configFromWindowGlobal[parameterName] !== undefined) {
      return configFromWindowGlobal[parameterName];
    }

    // look for configuration parameter in the URL calling our code:
    const urlQuery = UrlLib.parse(urlString, true).query;
    if (urlQuery !== undefined && urlQuery !== null && urlQuery[parameterName] !== undefined) {
      return urlQuery[parameterName];
    }

    // fall back to default value
    return defaultValue;
  }


  // --------- calling URL details -------------------------------------------------------

  /**
   * Get the protocol + hostname + port of the URL that called our code.
   * 
   * The method supports the protocols http, https, file and atom. For other 
   * protocols it logs an error and returns undefined.
   * 
   * The method does not include the path and parameters given in the URL.
   * 
   * Examples: 
   *  http://www.example.com:8080/some/path?withParameter=value is trimmed to
   *  http://www.example.com:8080
   * 
   *  file:///D:/develop/directory is trimmed to
   *  file:
   * 
   *  atom://... is trimmed to 
   *  atom:
   * 
   */
  static getCallingUrlWithoutPath() {
    return this.getUrlWithoutPathFromLocation(window.location)
  }

  static getUrlWithoutPathFromLocation(location) {
    switch (location.protocol) {
      case 'http:':
      case 'https:': {
        const portAddOn = location.port ? `:${location.port}` : '';
        return `${location.protocol}//${location.hostname}${portAddOn}`;
      }
      case 'file:':
        return location.protocol;
      case 'atom:':
        return location.protocol;
      default:
        console.error('Unknown protocol', location.protocol);
        return undefined;
    }
  }


  /**
   * Get the value of the specified parameter given in the URL calling our code.
   * 
   * The method returns undefined if there is no parameter with the specified name. 
   *
   * @param {*} paramName The name of the parameter to find a value for.
   */
  static getQueryParam(paramName) {
    const query = Utils.getQueryParams();
    return query[paramName];
  }

  /**
   * Build an object representing all parameters given in the URL calling our code.
   * 
   * Parameter names and values are properly URL-decoded.
   */
  static getQueryParams() {
    return Utils.getQueryParamsFromUrlString(window.location.href);
  }

  /**
   * Build an object representing all parameters in the given URL string.
   * 
   * Parameter names and values are properly URL-decoded.
   */
  static getQueryParamsFromUrlString(urlString) {
    return UrlLib.parse(urlString, true).query;
  }

  /**
   * Makes a request to the browser to set the element to fullscreen mode
   * 
   * @param {*} elem the element requested to go fullscreen
   * 
   */
  static openFullScreen(elem) {
    const fullscreenEnabled = document.fullscreenEnabled /* Standard syntax */
      || document.mozFullScreenEnabled /* Firefox */
      || document.webkitFullscreenEnabled /* Chrome, Safari and Opera */
      || document.msFullscreenEnabled /* IE/Edge */

    if (fullscreenEnabled) {
      if (elem.requestFullscreen) { /* Standard syntax */
        elem.requestFullscreen();
      } else if (elem.mozRequestFullScreen) { /* Firefox */
        elem.mozRequestFullScreen();
      } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) { /* IE/Edge */
        elem.msRequestFullscreen();
      }
    } else {
      console.warn("Current browser does not allow for fullscreen");
    }
  }

  /**
   *  Makes a request to the browser to exit fullscreen mode
   */
  static closeFullScreen() {
    if (document.fullscreenElement) {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.mozCancelFullScreen) { /* Firefox */
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
        document.webkitExitFullscreen();
      } else if (document.msExitFullscreen) { /* IE/Edge */
        document.msExitFullscreen();
      }
    }
  }

  static keyCodes = {
    Backspace: 8,
    Tab: 9,
    Enter: 13,
    Shift: 16,
    Ctrl: 17,
    Alt: 18,
    Escape: 27
  }

  static mapResourcePath(resources, path, isExternal) {
    return resources.map((resource) => {
      const resourceCopy = Utils.safeDeepCopy(resource);
      resourceCopy.path = `${path}/${resourceCopy.name}`;
      resourceCopy.isExternal = isExternal;
      return resourceCopy;
    });
  }

  /**
   * check the type of a variable to match a certain type.
   * @param {*} value 
   * @param {*} expectedType 
   */
  static checkType(value, expectedType) {
    const valueType = typeof value;

    if (expectedType === "integer" && valueType === "number") {
      return Number.isInteger(value);
    } else {
      return expectedType === valueType;
    }
  }

  static doScrollOnHTMLElement(x, y, sizerElement, scrollingElement, iframe) {
    const { scrollHeight, scrollWidth } = sizerElement;

    const clientHeight = iframe ? iframe.clientHeight : scrollingElement.clientHeight;
    const clientWidth = iframe ? iframe.clientWidth : scrollingElement.clientWidth;

    const scrollableHeight = scrollHeight - clientHeight;
    const scrollableWidth = scrollWidth - clientWidth;

    const { finalX, finalY } = Utils.calculateFinalScrollValue(x, y, scrollableHeight, scrollableWidth);

    Utils.doScroll(finalY, finalX, scrollingElement);
  }

  /**
   * Do scroll on an HTML element
   * @param {*} top 
   * @param {*} left 
   * @param {*} element 
   */
  static doScroll(top, left, element) {
    if (!element) {
      return;
    }
    element.scroll({
      top,
      left,
      behavior: 'smooth'
    });
  }

  /**
   * Computes the percentage if necessary on a scrollable container
   * @param {*} x px/% value
   * @param {*} y px/% value
   * @param {*} scrollableHeight 
   * @param {*} scrollableWidth 
   * @returns { finalX, finalY }
   */
  static calculateFinalScrollValue(x, y, scrollableHeight, scrollableWidth) {
    let finalX = x ? parseInt(x, 10) : undefined;
    let finalY = y ? parseInt(y, 10) : undefined;

    if (x && x.includes("%")) {
      finalX = Utils.computePercentage(scrollableWidth, parseFloat(x))
    }

    if (y && y.includes("%")) {
      finalY = Utils.computePercentage(scrollableHeight, parseFloat(y))
    }

    return {
      finalX,
      finalY
    }
  }

  /**
   * finds the percentage value of a number 
   * @param {Number} num 
   * @param {Number} percentage 
   * @returns {Number} value
   */
  static computePercentage(num, percentage) {
    return num * (percentage / 100);
  }

}
