import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import PropTypesHelper from '../PropTypesHelper';
import TreeUtils from './TreeUtils';
import RenderingHelper from '../RenderingHelper';
import CommonConfigHelper from '../../config/CommonConfigHelper';
import ComponentStateHelper from '../../state/ComponentStateHelper';
import StateAttributeAccess from '../../state/StateAttributeAccess';
import CommonActionsHelper from '../CommonActionsHelper';
import PageAreaRenderer from '../PageAreaRenderer';
import Utils from '../../utils/Utils';


export default class CbaTreeChildArea extends PureComponent {

  constructor(props) {
    super(props);
    const { runtime } = this.props;
    this.scrollableAreaRef = React.createRef();
    this.eventEmitter = runtime.eventEmitter;

    this.onClickHandler = this.onClickHandler.bind(this);
  }

  componentDidMount() {
    RenderingHelper.onMount(this);

    // Make our tree change handler listen to change events issued by our tree:
    const { path, config } = this.props;
    const { treePathId } = config;
    const treePath = TreeUtils.getTreePathFromChildComponent(treePathId, path);
    this.subscription = this.eventEmitter.addListener(TreeUtils.getTreeEventChangeName(treePath), this.handleTreeChange);
  }

  componentWillUnmount() {
    RenderingHelper.onUnmount(this);

    // Remove our tree change event subscription:
    this.subscription.remove();
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    RenderingHelper.onReceiveProps(this, nextProps);
  }

  onClickHandler(event) {
    CommonActionsHelper.doStandardOnClick(event, undefined, this);
  }

  onContextMenuHandler = (event) => {
    CommonActionsHelper.doContextMenuOpen(this, event);
  }

  /**
   * Callback handling changes in our associated tree.
   */
  handleTreeChange = () => {
    RenderingHelper.triggerRendering(this);
  }

  /**
   * Get the name of the currently embedded (i.e. displayed) page. 
   * 
   * The embedded page follows the selected node in the associated tree. 
   * We return 'undefined' if the currently selected node does not have a page assigned. 
   * 
   */
  static getEmbeddedPageName(path, config, runtime) {
    const { treePathId } = config;
    const treePath = TreeUtils.getTreePathFromChildComponent(treePathId, path);
    const { treeState } = TreeUtils.getTreeDataByPath(treePath, runtime);
    return treeState.currentPage || undefined;
  }

  scroll(x, y) {
    const scrollElement = this.scrollableAreaRef.current;
    if (!scrollElement) return;
    Utils.doScrollOnHTMLElement(x, y, scrollElement, scrollElement);
  }

  render() {
    const { config, runtime, path, orientation } = this.props;
    const currentPage = CbaTreeChildArea.getEmbeddedPageName(path, config, runtime);
    const emptyStyle = CommonConfigHelper.buildStyleByIndexPath(path, config, false, orientation, runtime);

    // TODO: CKI: factor that out in a common helper method shared with CbaPageArea
    const pathState = ComponentStateHelper.getState(this);
    const positionOnlyState = {};
    StateAttributeAccess.setPosition(positionOnlyState, StateAttributeAccess.extractPosition(pathState));


    if (currentPage === undefined) {
      return (
        <div
          style={emptyStyle}
          data-cba-id={config.pageEditId}
          onClick={event => CommonActionsHelper.doStandardOnClick(event, undefined, this)}
          title={CommonConfigHelper.buildTitle(config)}
          className={CommonConfigHelper.getCssClassNamesValue(config)}
        />
      )
    } else {
      return (
        <PageAreaRenderer
          onClick={this.onClickHandler}
          onContextMenu={this.onContextMenuHandler}
          orientation={orientation}
          embeddedPageName={currentPage}
          hasTabs={false}
          tabsBar={<span />}
          path={path}
          position={positionOnlyState}
          config={config}
          scrollableAreaRef={this.scrollableAreaRef}
          runtime={runtime}
        />
      )
    }
  }

}


CbaTreeChildArea.propTypes = {
  runtime: PropTypes.shape(PropTypesHelper.getStandardRuntimePropTypes()).isRequired,
  path: PropTypes.string.isRequired,
  config: PropTypes.shape(
    PropTypesHelper.addPropTypes(
      PropTypesHelper.getStandardConfigPropTypes(false),
      {
        treePathId: PropTypes.string.isRequired,
      }
    )
  ).isRequired,
  orientation: PropTypes.string.isRequired
}
