import React, { Component } from 'react';
import PropTypes from 'prop-types';
import debounce from 'lodash.debounce';
import LoggedInView from '../components/LoggedInView';
import Login from './Login';
import PageEventsObserver from '../components/PageEventsObserver';

export default class App extends Component {

  constructor(props) {
    super(props);

    // Make ourselves available via the runtime context:
    const { runtime, waitingMessageTexts } = this.props;
    this.runtime = runtime;
    runtime.app = this;

    this.waitingMessageTexts = waitingMessageTexts;
    this.scalingConfig = {
      alignmentHorizontal: "left",
      alignmentVertical: "top",
      scalingMode: "no-scaling"
    };

    // Possible display modes are: 'waiting', 'showLogin', 'showTask'.
    this.state = {
      displayMode: 'waiting',
      scaleValue: 1
    }

    // init observer that traces page visibility state
    runtime.pageEventsObserver = new PageEventsObserver(runtime);
    runtime.pageEventsObserver.initObserver();

  }

  /**
   * Recalculate our scaling factor and trigger a rendering
   * by putting it into the component state.
   * 
   * The method does nothing if we don't have a task running currently.
   */
  resize = () => {
    const { displayMode } = this.state;
    if (displayMode === 'showTask') {
      const scaleValue = this.computeScalingValue()
      this.setState(() => (Object.assign({}, {
        scaleValue
      })))
    }
  }

  componentDidMount() {
    window.addEventListener('resize', debounce(this.resize, 200))
  }

  componentWillUnmount() {
    window.removeEventListener('resize', debounce(this.resize, 200))
  }

  /**
   * Set new waiting messages texts.
   * 
   * @param {*} newValues The new waiting messages 'primary' and 'secondary'.
   */
  setWaitingMessageTexts(newValues) {
    this.waitingMessageTexts = newValues;
    const { displayMode } = this.state;
    if (displayMode === 'waiting') {
      this.showWaiting();
    }
  }

  /**
   * Show the waiting page.
   * 
   * The method implicitly stops a currently running task in the task manager.
   */
  showWaiting = () => {
    this.runtime.taskManager.stopTask();
    this.setState(prevState => ({
      displayMode: 'waiting',
    }));
  }

  /**
   * Show the login dialog.
   * 
   * @param {String} titleLabel The label to use as title of the login dialog.
   * @param {String} fieldLabel The label to use for the input field label in the login dialog.
   * @param {String} buttonLabel The label to use for the button label in the login dialog.
   * @param {function} loginDialogClosedHandler A callback that accepts the field value obtained by the login dialog.
   */
  showLogin = (titleLabel, fieldLabel, buttonLabel, loginDialogClosedHandler) => {
    this.setState(prevState => ({
      displayMode: 'showLogin',
      loginTitleLabel: titleLabel,
      loginFieldLabel: fieldLabel,
      loginButtonLabel: buttonLabel,
      loginDialogClosedHandler,
      loginDialogDisabled: false
    }));
  }

  /**
   * Show a running task.
   */
  showTask = (testName, itemName, taskName, settings, headerButtons, courseForNavigator, testsForNavigator) => {
    const taskId = this.runtime.taskManager.switchTask(testName, itemName, taskName, false);
    if (taskId === undefined) {
      console.info(`Could not switch to task ${testName}/${itemName}/${taskName}.`);
      return;
    }
    const scaleValue = this.computeScalingValue(this.scalingConfig.scalingMode);

    App.dumpTasksViewConfigurationToTraceLog(courseForNavigator, testsForNavigator, settings, headerButtons, this.runtime.traceLogBuffer);
    this.setState(prevState => ({
      displayMode: 'showTask',
      settings,
      headerButtons,
      courseForNavigator,
      testsForNavigator,
      scaleValue
    }));
  }

  /**
   * Show a replay of a task.
   */
  showReplay = (testName, itemName, taskName, settings, headerButtons, courseForNavigator, testsForNavigator) => {
    const taskId = this.runtime.taskManager.switchTask(testName, itemName, taskName, true);
    if (taskId === undefined) {
      console.info(`Could not switch replay to task ${testName}/${itemName}/${taskName}.`);
      return;
    }
    const scaleValue = this.computeScalingValue(this.scalingConfig.scalingMode);

    App.dumpTasksViewConfigurationToTraceLog(courseForNavigator, testsForNavigator, settings, headerButtons, this.runtime.traceLogBuffer);
    this.setState(prevState => ({
      displayMode: 'showTask',
      settings,
      headerButtons,
      courseForNavigator,
      testsForNavigator,
      scaleValue
    }));
  }

  /**
   * Set the scaling configuration that we will use to scale the display in task display mode.
   * 
   * The setting is not active for the other display modes.
   * The setting is not applied immediately to an already running task display mode. Call the resize method to trigger 
   * the scaling update on a running task.
   */
  setScalingConfig = (scalingConfig) => {
    this.scalingConfig = scalingConfig;
  }

  /**
   * Calculate the current scaling factor. 
   * 
   * The scaling factor depends 
   *  - on the current scaling configuration and 
   *  - the currently available display size and
   *  - the required display size of the currently running item. 
   * 
   * Since this method needs an item in place, you should not call it without a task running currently
   * in the task manager.
   */
  computeScalingValue = () => {
    const { runtime } = this.props;
    let value;

    const horizontalScale = window.innerWidth / runtime.taskManager.getTopLevelConfiguration().itemWidth;
    const verticalScale = window.innerHeight / runtime.taskManager.getTopLevelConfiguration().itemHeight;

    switch (this.scalingConfig.scalingMode) {
      case "no-scaling": value = 1; break;
      case "scale-up":
        if (horizontalScale > 1 && verticalScale > 1) {
          value = horizontalScale < verticalScale ? horizontalScale : verticalScale
        } else {
          value = 1;
        }
        break;
      case "scale-down":
        if (horizontalScale < 1 || verticalScale < 1) {
          value = horizontalScale < verticalScale ? horizontalScale : verticalScale;
        } else {
          value = 1;
        }
        break;
      case "scale-up-down":
        value = horizontalScale < verticalScale ? horizontalScale : verticalScale;
        break;
      default: value = 1;
    }

    return value;
  }

  getScalingAlignmentCssClass = () => {
    const { alignmentHorizontal, alignmentVertical } = this.scalingConfig;

    let cssClass = "";

    switch (alignmentHorizontal) {
      case "left": cssClass += "left"; break;
      case "center": cssClass += "horizontal-center"; break;
      case "right": cssClass += "right"; break;
      default: cssClass += "left";
    }

    cssClass += " ";

    switch (alignmentVertical) {
      case "top": cssClass += "top"; break;
      case "center": cssClass += "vertical-center"; break;
      case "bottom": cssClass += "bottom"; break;
      default: cssClass += "top";
    }

    return cssClass;
  }


  /** 
   * The login screen is filled in -> Disable login screen and run callback with obtained field value.
   */
  handleLoginClick = (fieldValue) => {
    const { loginDialogClosedHandler } = this.state;
    this.setState(prevState => ({
      loginDialogDisabled: true
    }))
    loginDialogClosedHandler(fieldValue);
  }

  /**
   * Dump the configuration of the tasks view to the trace log.
   * 
   * @param {*} courseForNavigator The list of tests in the navigator menu.
   * @param {*} testsForNavigator The test configurations used by the navigator menu.
   * @param {*} settings The general settings used.
   * @param {*} headerButtons The configurable header buttons used.
   * @param {*} traceLogBuffer The trace log buffer to dump to.
   */
  static dumpTasksViewConfigurationToTraceLog(courseForNavigator, testsForNavigator, settings, headerButtons, traceLogBuffer) {
    traceLogBuffer.reportEvent('TasksViewVisible', new Date(), {
      settings,
      headerButtons,
      upperHeaderMenu: courseForNavigator,
      lowerHeaderMenu: testsForNavigator,
    })
  }

  renderWaiting = () => {
    // configNok -> wait for the configuration setting event to arrive:
    const primary = this.waitingMessageTexts === undefined ? "" : this.waitingMessageTexts.primary
    const secondary = this.waitingMessageTexts === undefined ? "" : this.waitingMessageTexts.secondary

    return (
      <div className="appDivContainer top left">
        <div className="appDiv">
          {primary}
          <br />
          <br />
          {secondary}
        </div>
      </div>
    )
  };

  renderLogin = () => {
    const { loginTitleLabel, loginFieldLabel, loginButtonLabel, loginDialogDisabled } = this.state;
    return (
      <div className="appDivContainer horizontal-center vertical-center">
        <Login
          handleLogin={this.handleLoginClick}
          titleLabel={loginTitleLabel}
          fieldLabel={loginFieldLabel}
          buttonLabel={loginButtonLabel}
          disabled={loginDialogDisabled}
        />
      </div>
    )
  };

  renderTask = () => {
    const { runtime } = this.props;

    const { settings, headerButtons, courseForNavigator, testsForNavigator, scaleValue } = this.state;

    return (
      <div className={`appDivContainer ${this.getScalingAlignmentCssClass()} ${scaleValue === 1 ? 'overflow-auto' : ''}`}>
        <LoggedInView
          courses={courseForNavigator}
          tests={testsForNavigator}
          settings={settings}
          headerButtons={headerButtons}
          runtime={runtime}
          scaleValue={scaleValue}
        />
      </div>
    );
  };

  render() {
    const { displayMode } = this.state;

    switch (displayMode) {
      case 'waiting':
        return this.renderWaiting();
      case 'showLogin':
        return this.renderLogin();
      case 'showTask':
        return this.renderTask();
      default:
        return this.renderWaiting();
    }
  }

}

App.propTypes = {
  runtime: PropTypes.object.isRequired,
  waitingMessageTexts: PropTypes.object.isRequired
}
