import React from 'react';
import PropTypes from 'prop-types';

import './WebTab.css';

const WebTab = (props) => {
  const { tabName, closeTab, isActive, onClick, icon, width, closable } = props;

  const webTabClass = `webTab ${isActive ? "active" : ""}`;
  const webTabStyle = {
    width
  };

  return (
    <div title={tabName} className={webTabClass} onClick={onClick} style={webTabStyle}>
      {icon === undefined ? undefined : <img src={icon} alt="" height="100%" width="auto" />}
      <span>{tabName}</span>
      <i className={closable ? "show" : "hide"} onClick={closeTab} />
    </div>
  )
}

WebTab.propTypes = {
  tabName: PropTypes.string.isRequired,
  closeTab: PropTypes.func.isRequired,
  isActive: PropTypes.bool.isRequired,
  onClick: PropTypes.func.isRequired,
  closable: PropTypes.bool.isRequired,
  width: PropTypes.number.isRequired,
  icon: PropTypes.string,
}

WebTab.defaultProps = {
  icon: undefined
};

export default WebTab;
