import StateManagerHelper from "./StateManagerHelper";
import TraceLogHelper from "./TraceLogHelper";

export default class TaskNavigatorStateManager {

  constructor() {
    this.testEntriesList = {};
  }

  saveTestState = (testName, state, runtime) => {
    this.testEntriesList[testName] = StateManagerHelper.deepCopy(state);

    TraceLogHelper.dumpStateChangeToTrace(
      'TaskNavigatorStateManagerState',
      {
        name: testName,
        value: this.testEntriesList[testName]
      },
      runtime.traceLogBuffer
    );

  }

  getTestState = testName => StateManagerHelper.deepCopy(this.testEntriesList[testName])

  /**
   * Get the full state for all existing tasks.
   * 
   * Use the result of this method as parameter to preloadTasksState to preload another instance to our current state. 
   */
   getAllTasksState = () => StateManagerHelper.deepCopy(this.testEntriesList);

   /**
    * Drop all state.
    */
   clearTasksState = () => {
     this.testEntriesList = {};
   };

   /**
    * Preload the state returned by a call to getAllTasksState.
    */
   preloadTasksState = (allTasksState) => {
     this.testEntriesList = StateManagerHelper.deepCopy(allTasksState);
   }

  /**
   * Inject a change for a test entry triggered by a StateChange step during replay.
   */
  injectStateChangeAtReplay = (testName, state) => {
    this.testEntriesList[testName] = StateManagerHelper.deepCopy(state)
  }

}
