import Controller from './controller/Controller';
import TaskPlayer from './preamble/TaskPlayer';
import Utils from './utils/Utils';


/**
 * Determine whether we should use the internal controller to control the task player
 * or wait for an extenral controller to take over.
 * 
 * The method checks settings given via the cba_runtime_config attribute
 * of the global window. 
 */
function shouldActivateInternalController() {
  const controllerMode = Utils.getControllerMode();
  const useExternalController = controllerMode === 'external-controller';
  if (useExternalController) {
    console.log('Wait for external controller to take over...');
  }
  return !useExternalController;
}

/**
 * Start the task player and send the ready signal.
 */
function startTaskPlayer(taskPlayer) {
  taskPlayer.sendPlayerReady();
  taskPlayer.runPlayer();
}

/**
 * Start up the CBA runtime.
 */
function startUp() {
  const eventReceiverConfiguration = Utils.getEventReceiverConfiguration();
  const taskPlayer = new TaskPlayer(eventReceiverConfiguration.eventTargetWindow, eventReceiverConfiguration.eventDomainUri);
  if (shouldActivateInternalController()) {
    // Activate the internal controller which will control the task player.
    const internalController = new Controller();
    internalController.runController(taskPlayer);
  } else if (eventReceiverConfiguration.startTaskPlayerEvent) {
    // Wait for the configured 'start' event and start the task player once the event arrives.
    // The task player will wait for commands from an external controller.
    window.addEventListener(eventReceiverConfiguration.startTaskPlayerEvent, (startEvent) => {
      startTaskPlayer(taskPlayer);
    });
  } else {
    // Start the task player immediately. The task player will wait for commands from an external controller.
    startTaskPlayer(taskPlayer);
  }
}

startUp();
